/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
vdl-popover-predefined button:focus vdl-icon {
  color: #005a99; }

vdl-popover-predefined button[vdl-tertiary-button] {
  padding: 0;
  background-color: transparent;
  border-color: transparent;
  line-height: 0;
  color: black;
  cursor: default; }
  vdl-popover-predefined button[vdl-tertiary-button]:hover, vdl-popover-predefined button[vdl-tertiary-button].hover {
    color: black;
    background-color: transparent;
    border-color: transparent; }
  vdl-popover-predefined button[vdl-tertiary-button]:focus, vdl-popover-predefined button[vdl-tertiary-button].focus {
    color: black;
    background-color: transparent;
    border-color: transparent; }
  vdl-popover-predefined button[vdl-tertiary-button]:active, vdl-popover-predefined button[vdl-tertiary-button].active {
    color: black;
    background-color: transparent;
    border-color: transparent; }

vdl-popover-predefined .vdl-info-circle-icon {
  color: #414142;
  cursor: pointer; }

vdl-popover-predefined .vdl-error-circle-icon {
  color: #b1181e;
  cursor: pointer; }

vdl-popover-predefined .vdl-custom-icon {
  color: #414142;
  cursor: pointer; }
